// 模拟当前登录用户的userid
const uid = 1;

import $T from '@/common/time.js';
export default {
	props:{
		item:Object,
		index:Number,
		pretime:[Number,String]
	},
	computed: {
		// 是否是登录用户本人
		isSelf() {
			return uid === this.item.user_id
		},
		// 转化时间
		shortTime(){
			return $T.getChatTime(this.item.create_time,this.pretime)
		}
	},
}
